import React from "react"
import { Heading } from "../common/Heading"
import { skills } from "../data/dummydata"

export const Skills = () => {
  const animation = ["fade-down-right", "fade-down", "fade-down-left", "fade-right", "zoom-out", "fade-left", "fade-up-right", "fade-up", "fade-up-left"]
  return (
    <>
      <section className='skills' style={{ height: "auto" }}>
        <div className='container'>
          <Heading title='Skills' />
          <div className='content grid3'>
            {skills.map((item) => (
              <div className='box' data-aos={animation[item.id - 1]} data-aos-duration="1000" data-aos-easing="ease-in-sine">
                <i>{item.icon}</i>
                <h3>{item.title}</h3>
                <p>{item.desc}</p>
              </div>
            ))}
          </div>
        </div>
      </section>
    </>
  )
}
