import { Heading } from "../common/Heading"
import { certificates } from "../data/dummydata"

export const Certificates = () => {
    const animation = ["fade-right", "fade-down", "fade-down", "fade-left"]
    return (
        <>
            <article>
                <div className='container' style={{ marginTop: "50px" }}>
                    <Heading title='Certificates' />
                    <div className='content grid2'>
                        {certificates.map((item) => (
                            <div className='box' data-aos={animation[(item.id - 1) % 4]} data-aos-duration="1000" data-aos-easing="ease-in-sine">
                                <div className='img'>
                                    <img src={item.cover} alt='' />
                                </div>
                                <div className='overlay' onClick={() => window.open(item.link, "_blank")}>
                                    <h3>{item.title}</h3>
                                    <span>{item.name}</span>
                                    {item.icon}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </article>
        </>
    )
}
